.singerDetails {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 10;
  background-color: #fff;
  transform: rotate(-90deg);
  transition: all 0.5s;
  transform-origin: top right;

  .am-notice-bar {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 100000;
  }

  .top {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999;
    display: flex;
    align-items: center;
    color: #f1f1f1;
    padding: 10px 5px;
    font-size: 16px;
  }

  .cover {
    position: relative;
    height: 295px;
    background-size: 100% 100%;

    .btn {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: 30px;
      width: 120px;
      height: 40px;
      line-height: 40px;
      border-radius: 20px;
      border: none;
      background-color: #d1453a;
      color: #efeded;

      &::before {
        border: none;
      }

      .icon-add {
        font-size: 16px;
      }
    }

    .btn.am-button-active {
      background-color: #be433a;
      color: #c5bebe;
    }
  }

  .songMenuList {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 50px;
    z-index: 999;
    overflow: auto;
    margin-top: -10px;

    &::-webkit-scrollbar {
      display: none;
    }

    .am-list {
      padding-top: 240px;
      padding-bottom: 100px;

      .am-list-body {
        overflow: hidden;
        border-radius: 15px 15px 0 0;
      }
    }

    .title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 45px;
      color: #333;
      border-bottom: 1px solid #f1f1f1;
      margin-left: 10px;

      i.icon-bofang {
        font-size: 24px;
        margin: 0 10px;
      }

      span {
        font-size: 16px;

        span {
          font-size: 12px;
          color: #bba9a9;
        }
      }
    }

    .box {
      display: flex;
      align-items: center;

      .index {
        text-align: center;
        width: 60px;
      }

      .am-list-item {
        padding-left: 0;
        background-color: #fff;
        flex: 1;
        
        .am-list-content {
          p{
            line-height: 28px;
          }
          .am-list-brief {
            font-size: 12px;
            margin: 0;
          }
        }
        .am-list-line {
          padding: 5px;


        }
      }
    }

    .am-icon-loading {
      position: fixed;
      width: 40px;
      height: 40px;
      left: 50%;
      margin-left: -20px;
      top: 70%;
    }
  }
}
